.auth-type-button {
  display: block;
  width: 100%;
  padding: 12px 24px;
  margin: 8px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.2s;
}

.auth-type-button:hover {
  background-color: #f5f5f5;
}

.auth-type-button:active {
  background-color: #e0e0e0;
}
